.App {
  text-align: center;
  margin: 0;
  padding: 0;
  overflow: hidden;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

html,
body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: 62.5%;
  background-color: white;
  font-family: 'Montserrat', sans-serif;
  flex-wrap: nowrap;
  width: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0 10px;
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  margin: 20px;
  width: auto;

}

.component {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  width: auto;
}


h1 {
  color: #282c34;
  font-size: 5rem;
  font-family: 'Playfair Display', serif;
}

h2 {
  color: #282c34;
  font-size: 3rem;
  font-family: 'Montserrat', sans-serif;
}

p {
  font-size: 1.6rem;
}

button {
  background-color: #282c34;
  color: white;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: 500;
  font-family: 'Montserrat', sans-serif;
  box-shadow: 
    0 1px 1px rgba(0, 0, 0, 0.2),
    0 2px 2px rgba(0, 0, 0, 0.2), 
    0 4px 4px rgba(0, 0, 0, 0.2);
}

input {
  border: 1px solid lightgray;
  box-shadow: 
  inset 0 1px 2px rgba(0,0,0,0.2), 
  0 -1px 1px #FFF, 
  0 1px 0 #FFF;
  background-color: white;
  border-radius: 5px;
  font-size: 1.8rem;
  padding: 5px 10px;
  margin: 10px 20px;
  outline: none;
  
}
